ce-button.outline:active {\n    opacity: 0.3;\n}\n\n.rce-button.transparent {\n    background: rgba(0, 0, 0, 0) !important;\n}\n\n.rce-button.transparent:hover {\n    opacity: 0.6;\n}\n\n.rce-button.transparent:active {\n    opacity: 0.3;\n}\n\n.rce-button-icon {\n    position: relative;\n    font-size: 18px;\n    display: flex;\n    padding: 0 3px;\n}\n\n.rce-button-badge{\n    border-radius: 4px;\n    padding: 4px;\n    background: #f64b34;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: center;\n    position: absolute;\n    right: -7px;\n    top:-7px;\n    font-size: 10px;\n}\n\n.rce-button.circle {\n    min-width: 35px;\n    min-height: 35px;\n    border: 1px solid #3979aa;\n    border-radius: 100%;\n}\n.rce-container-mbox {\n    flex-direction: column;\n    display: block;\n    overflow-x: hidden;\n    min-width: 200px;\n}\n\n.rce-mbox-forward {\n    width: 30px;\n    height: 30px;\n    border-radius: 20px;\n    background: #fff;\n    position: absolute;\n    /*display: none;*/\n    flex-direction: row;\n    align-self: center;\n    align-items: center;\n    justify-content: center;\n    box-shadow: 0 0 5px 0 rgba(164, 164, 164, 1);\n    cursor: pointer;\n    transition: all 0.3s ease;\n    top: 0;\n    bottom: 0;\n    margin: auto;\n}\n\n.rce-mbox-forward-left {\n    display: flex;\n    opacity: 0;\n    visibility: hidden;\n    left: -50px;\n}\n\n.rce-mbox-forward-right {\n    display: flex;\n    opacity: 0;\n    visibility: hidden;\n    right: -50px;\n}\n\n.rce-mbox-reply-btn-left {\n    display: flex;\n    opacity: 0;\n    visibility: hidden;\n    left: -85px;\n}\n\n.rce-mbox-reply-btn-right {\n    display: flex;\n    opacity: 0;\n    visibility: hidden;\n    right: -85px;\n}\n\n.rce-container-mbox:hover .rce-mbox-forward-left {\n    opacity: 1;\n    visibility: visible;\n}\n\n.rce-container-mbox:hover .rce-mbox-forward-right {\n    opacity: 1;\n    visibility: visible;\n}\n\n.rce-container-mbox:hover .rce-mbox-reply-btn-left {\n    opacity: 1;\n    visibility: visible;\n}\n\n.rce-container-mbox:hover .rce-mbox-reply-btn-right {\n    opacity: 1;\n    visibility: visible;\n}\n\n.rce-mbox {\n    position: relative;\n    background: white;\n    border-radius: 5px;\n    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .2);\n    border-top-left-radius: 0px;\n    margin-left: 20px;\n    margin-right: 5px;\n    margin-top: 3px;\n    flex-direction: column;\n    margin-bottom: 3px;\n    padding: 6px 9px 8px 9px;\n    float: left;\n    min-width: 140px;\n}\n\n.rce-mbox.message-focus {\n    animation-iteration-count: 2;\n    -webkit-animation-iteration-count: 2;\n    -webkit-animation-duration: 1s;\n    animation-name: message-box-default-focus;\n    animation-duration: 1s;\n}\n\n@-webkit-keyframes message-box-default-focus {\n    from {background-color: #fff;}\n    to {background-color: #dfdfdf;}\n}\n\n.rce-mbox-body {\n    margin: 0;\n    padding: 0;\n    position: relative;\n}\n\n.rce-mbox.rce-mbox-right {\n    float: right;\n    margin-left: 5px;\n    margin-right: 20px;\n    border-top-right-radius: 0px;\n    border-top-left-radius: 5px;\n}\n\n.rce-mbox.rce-mbox-right.message-focus {\n    animation-iteration-count: 2;\n    -webkit-animation-iteration-count: 2;\n    -webkit-animation-duration: 1s;\n    animation-name: message-box-right-focus;\n    animation-duration: 1s;\n}\n\n@-webkit-keyframes message-box-right-focus {\n    from {background-color: #d4f1fb;}\n    to {background-color: #b8dae6;}\n}\n\n.rce-mbox-text {\n    font-size: 13.6px;\n    word-break: break-word;\n}\n\n.rce-mbox-text:after {\n    content: \"\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\\A0\"\n}\n\n.rce-mbox-time {\n    text-align: right;\n    color: rgba(0, 0, 0, 0.45);\n    font-size: 12px;\n    position: absolute;\n    right: -4px;\n    bottom: -5px;\n}\n\n.rce-mbox-time.non-copiable:before {\n    content: attr(data-text);\n}\n\n.rce-mbox-time-block {\n    /*position: relative;*/\n    right: 0;\n    bottom: 0;\n    left: 0;\n    margin-right: -6px;\n    margin-left: -6px;\n    padding-top: 5px;\n    padding-right: 3px;\n    padding-bottom: 2px;\n    background: linear-gradient(to top, rgba(0,0,0,0.33), transparent);\n    border-bottom-left-radius: 5px;\n    border-bottom-right-radius: 5px;\n    color: #fff;\n}\n\n.rce-mbox--clear-padding {\n    padding-bottom: 3px;\n}\n\n.rce-mbox.rce-mbox--clear-notch {\n    border-radius: 5px 5px 5px 5px !important;\n}\n\n.rce-mbox-right-notch {\n    position: absolute;\n    right: -14px;\n    top: 0px;\n    width: 15px;\n    height: 15px;\n    fill: white;\n    filter: drop-shadow( 2px 0px 1px rgba(0, 0, 0, .2));\n}\n\n.rce-mbox-right-notch.message-focus {\n    animation-iteration-count: 2;\n    -webkit-animation-iteration-count: 2;\n    -webkit-animation-duration: 1s;\n    animation-name: message-right-notch-focus;\n    animation-duration: 1s;\n}\n\n@-webkit-keyframes message-right-notch-focus {\n    from {fill: #d4f1fb;}\n    to {fill: #b8dae6;}\n}\n\n.rce-mbox-left-notch {\n    position: absolute;\n    left: -14px;\n    top: 0px;\n    width: 15px;\n    height: 15px;\n    fill: white;\n}\n\n.rce-mbox-left-notch.message-focus {\n    animation-iteration-count: 2;\n    -webkit-animation-iteration-count: 2;\n    -webkit-animation-duration: 1s;\n    animation-name: message-left-notch-focus;\n    animation-duration: 1s;\n}\n\n@-webkit-keyframes message-left-notch-focus {\n    from {fill: #fff;}\n    to {fill: #dfdfdf;}\n}\n\n.rce-mbox-title {\n    margin: 0;\n    margin-bottom: 8px;\n    font-weight: 500;\n    font-size: 13px;\n    color: #4f81a1;\n    user-select: none;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n}\n\n.rce-mbox-title:hover {\n    text-decoration: underline;\n}\n\n.rce-mbox-title--clear {\n    margin-bottom: 5px;\n}\n\n.rce-mbox-status {\n    margin-left: 3px;\n    font-size: 15px;\n}\n\n.rce-mbox-title > .rce-avatar-container {\n    margin-rig